import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomAuthComponent } from './custom-auth.component';
import { NbAuthComponent } from '@nebular/auth';
import { ResolveUserComponent } from '../loans-app/loans/resolve-user/resolve-user.component';

const routes: Routes = [
  {
    path: '',
    component: NbAuthComponent,
    children: [
      {
        path: 'login',
        component: ResolveUserComponent, // <---
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CustomAuthRoutingModule { }
